.wrapper{
    width: 45%;
    background-color: #fff;
    border-radius: 8px;
    border: 1px solid rgb(196, 196, 196);
}




.title{
    font-size: 20px;
    margin: 10px 0 0 40px;

}

.friendPhoto{
    width: 80px;
    height: 80px;
    border-radius: 50%;
}
.friendName{
    text-align: center;
}
.friendsBox{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 0 auto;
    width: 100%; 

}

.friend{
    margin: 20px;
    width: fit-content;
}



@media (max-width:1130px) {
    .wrapper{
        width: 100%;
        margin: 20px 0 0 0;
    }
    .friend{
        margin: 10px;
    }
    .friendPhoto{
        width: 90px;
        height: 90px;
    }
    .friendName{
        width: 100%;
        text-align: center;
    }
}



@media (max-width:880px) {
    .wrapper{
        width: 100%;
    }
    .friendPhoto{
        width: 90px;
        height: 90px;
        padding: 5px;
        margin: auto;
    }
    .friend{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100px;
        flex-wrap: wrap;
    }

    .friendName{
        width: 100%;
        text-align: center;
    }
}


@media (max-width:765px) {
    .wrapper{
        width: 100%;
        padding: 0 10%;
        margin: 20px 0 0;
        justify-content: space-evenly;
    }

    .friendPhoto{
        width: 90px;
        height: 90px;
        margin: auto;
    }
    .friend{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        width: 30%;
 
    }
}



@media (max-width:500px) {
    .wrapper{
        width: 100%;
        padding: 0 5%;
        margin: 20px 0 0;
        justify-content: space-evenly;
    }

    .friendPhoto{
        width: 50%;
        height: 50%;
        margin: auto;
    }
    .friend{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        width: 40%;
        font-size: 15px;
 
    }
}