
.wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    background-color: #fff;
    border-radius: 8px;
    border: 1px solid rgb(196, 196, 196);
    width: 50%;
}





.fullName{
    padding: 10px;
    font-size: 20px;
    text-transform: uppercase;
    color: black;
    font-weight: 300;
    width: 100%;
    text-align: center;
    
}


.userPic{
    height: 200px;
    width: 200px;
    margin-top: -100px;
    outline: 4px solid rgba(239, 239, 239);
    border-radius: 50%;

}

.ownerUserPic{
    width: 200px;
    height: 200px;
    margin-top: -100px;
    border-radius: 50%;
    transition: filter 200ms ease-in-out;
    
}




.ownerUserPic:hover{
    transition-property: filter;
    outline: 6px solid rgba(0, 0, 0, 0.521);
    cursor: pointer;
    filter: brightness(0.7);
}


.ownerUserPic:active{
    outline: 1px solid rgba(0, 0, 0, 0.459);
}
.aboutMe{
    padding: 10px;
    width: 100%;
}

.socials{
    padding: 0 0 0 10px;
}


.contacts{
    display: flex;
    height: 40px; 
    width: 100%;
    align-items: flex-end;
    padding: 0 0 0 10px;


}


.status{
    display: flex;
    justify-content: center;
    cursor: pointer;
    width: 270px;
    overflow: hidden;
    text-align: center;
}
.location{
    width: 100%;

}
.locationIcon{
    width: 15px;
    height: 15px;
    padding: 0 0 0 10px;
}
.iconsBox{
    width: 30px;

}

.icons{
    width: 20px;
    padding: 5px;

}

.iconsHover{
    width: 25px;
    padding: 3px;
}

.nameAndStatus{ 
    display: flex;
    flex-wrap: wrap;
    width: 200px;
    height: fit-content;

}


.imageBox{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 200px;
    height: 150px;
    margin:  15px ;
    flex-wrap: nowrap;
}

.uploadPhotoLabelContainer{
    margin: 20px 0 0 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#uploadPhoto {
    opacity: 0;
    margin: -25px 0 0 0;
    z-index: -1;
    width: 100%;
 }

 #uploadPhotoLabel{
    background-color: rgb(5, 85, 206);
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    max-width: 100%;
    padding: 5px;
    height: fit-content;
 }

 label:hover{
    filter: brightness(1.2);
 }

 @media (max-width:1130px) {
    .wrapper{
        width: 100%;
    }
}


@media (max-width:890px) {
    .ownerUserPic{
        width: 150px;
        height: 150px;
        margin:  auto  ;
        margin-top: -50px;
        border-radius: 50%;
        transition: filter 200ms ease-in-out;
    }
    .imageBox{
        width: 100%;
        height: fit-content;
        display: flex;
        justify-content: center;
    }
    .ownerUserPic{
        width: 200px;
        height: 200px;   
        margin: 0 auto;   
    }
    .nameAndStatus{ 
        width: 100%;
       justify-content: center;
    
    }
}

